}


.ant-carousel .slick-slide img{
  height: 150px;
  width:100%;
}
.ant-carousel .slick-slide {
  overflow: hidden;
  height: 150px;
  position: relative;
}

.ant-carousel .slick-slide p {
  color: #fff;
  position: absolute;
  top:50px;
  left:185px
}
.business{
