  display:flex;
  -ms-flex-align: center;
      align-items: center;
  height: 80px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  border-bottom: solid 1px #333;
}
.business div {
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 100%;
}
.business div:first-child{
  border-right: solid 1px #333;
