.sence{
  height: 400px;
}
.ant-collapse{
  width:100%;
  border-radius: 0;
}
.ant-collapse-content-box img {
  width:100%;
  height: 100px;
  opacity: 0.5;
}
.ant-collapse-content > .ant-collapse-content-box {
  padding: 0;
  position: relative;
}
.ant-collapse-content > .ant-collapse-content-box p{
